import "./AboutContentStyles.css";
import { Link } from "react-router-dom";
import pro3 from "../Assets/download1.jfif";
function AboutContent() {
  return (
    <div className="about">
      <div className="left">
        <h1> Who Am I?</h1>
        <p>
          Im a react front-end developer.I create responsive secure website for
          my clients
        </p>
        <Link to="/contact">
          <button className="btn">Contact</button>
        </Link>
      </div>
      <div className="right">
        <div className="img-container">
          <div className="img-stack-top">
            <img
              src="https://colorlib.com/wp/wp-content/uploads/sites/2/featured_react-templates.jpg"
              className="img"
              alt="true"
            />
          </div>
          <div className="img-stack-bottom">
            <img src={pro3} className="img" alt="true" />
          </div>
        </div>
      </div>
    </div>
  );
}
export default AboutContent;
